<template>
    <div>
        <Form :model="form" :label-width="80">
            <Row span="24">
                <template v-for="item, index in formJson" :key="index">
                    <Col v-if="item.type === 'FORM_INPUT'" :span="item.span">
                        <FormItem :label="item.name">
                            <Input v-model="form[item.key]" :placeholder="item.name" />
                        </FormItem>
                    </Col>
                    <Col v-if="item.type === 'FORM_SELECT'" :span="item.span">
                        <FormItem :label="item.name">
                            <Select v-model="form[item.key]">
                                <Option v-for="v in item.config.selectlist" :key="v.value" :value="v.value">{{v.name}}</Option>
                            </Select>
                        </FormItem>
                    </Col>
                </template>
            </Row>
        </Form>
        {{form}}
    </div>
</template>
<script>
import { defineComponent, reactive } from 'vue';
export default defineComponent({
    name: 'base-form',
    setup() {
        const form = reactive({})
        const formJson = reactive([
            {
                key: 'input',
                name: '输入框',
                type: 'FORM_INPUT',
                span: '12'
            },
            {
                key: 'input1',
                name: '输入框1',
                type: 'FORM_INPUT',
                span: '12'
            },
            {
                key: 'select',
                name: '下拉框',
                type: 'FORM_SELECT',
                span: '24',
                config: {
                    selectlist: [
                        {
                            name: '下拉1',
                            value: 1
                        },
                        {
                            name: '下拉2',
                            value: 2
                        }
                    ]
                }
            }
        ])
        return {
            formJson,
            form
        }
    }
})
</script>